.lightbox {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    opacity: 0;
    pointer-events: none;
    transition: opacity var(--animation-duration) var(--timing-function);
  }
  
  .lightbox.is-open {
    opacity: 1;
    pointer-events: initial;
  }
  
  .lightbox__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
  }
  
  .lightbox__content {
    transform: scale(0.95);
    transition: transform var(--animation-duration) var(--timing-function);
  }
  
  .lightbox.is-open .lightbox__content {
    transform: scale(1);
  }
  
  .lightbox__image {
    display: block;
    width: auto;
    height: auto;
    max-height: 100vh;
    max-width: 100vw;
  }
  
  .lightbox__button {
    position: absolute;
    top: 20px;
    right: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    padding: 0;
    margin: 0;
    border: none;
    border-radius: 50%;
    background-color: transparent;
    color: #fff;
    cursor: pointer;
    transition: background-color var(--animation-duration) var(--timing-function);
    outline: none;
    background-image: url('../images/icon-close.svg');
    background-position: center;
    background-size: 60%;
    background-repeat: no-repeat;
  }
  
  .lightbox__button:hover,
  .lightbox__button:focus {
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .left-arrow {
    position: absolute;
    top: 50%;
    left: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    padding: 0;
    margin: 0;
    border: none;
    border-radius: 50%;
    background-color: transparent;
    color: #fff;
    cursor: pointer;
    transition: background-color var(--animation-duration) var(--timing-function);
    outline: none;
    background-image: url('../images/left-arrow.svg');
    background-position: center;
    background-size: 60%;
    background-repeat: no-repeat;
  }
  
  .left-arrow:hover,
  .left-arrow:focus {
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .right-arrow {
    position: absolute;
    top: 50%;
    right: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    padding: 0;
    margin: 0;
    border: none;
    border-radius: 50%;
    background-color: transparent;
    color: #fff;
    cursor: pointer;
    transition: background-color var(--animation-duration) var(--timing-function);
    outline: none;
    background-image: url('../images/right-arrow.svg');
    background-position: center;
    background-size: 60%;
    background-repeat: no-repeat;
  }
  
  .right-arrow:hover,
  .right-arrow:focus {
    background-color: rgba(0, 0, 0, 0.5);
  }
  